document.addEventListener('DOMContentLoaded', function() {
 eventListeners();
});
function eventListeners() {
    
}

const inputMasa = document.querySelector('.inputMasa')
const ResultadoParteI = document.querySelector('.resultadoParteI')
const btnFinalizado = document.querySelector('.btnFinalizado')

let peso = 0;
inputMasa.addEventListener('input', e =>{
    const masa = parseFloat(e.target.value);
    const pesoContenedor = document.createElement('P')
    peso = masa * 9.8
    peso = Math.round(peso * 100) / 100 

    if(isNaN(peso)){
        ResultadoParteI.textContent = "";
    }else{
        ResultadoParteI.textContent = `El resultado es ${peso}`;
    }
})


// Parte II
const inputT1 = document.querySelector('.cosenoT1')
const inputT2 = document.querySelector('.cosenoT2')
const botonParteII = document.querySelector('.botonParteII')
const resultadoParteII = document.querySelector('.resultadoParteII')
const resultadoParteIII = document.querySelector('.resultadoParteIII')




let cosenoT1 = 0;
let cosenoT2 = 0;
inputT1.addEventListener('input', e =>{
    const coseno = e.target.value;
    
    // cos
    cosenoT1 = Math.cos(coseno * Math.PI / 180)
    cosenoT1 = Math.round(cosenoT1 * 100) / 100
    
    // sen
    cosenoT1Sen = Math.sin(coseno * Math.PI / 180)  
    cosenoT1Sen = Math.round(cosenoT1Sen * 100) / 100 
})

inputT2.addEventListener('input', e =>{
    const coseno = e.target.value;

    // cos
    cosenoT2 = Math.cos(coseno * Math.PI / 180)  
    cosenoT2 = Math.round(cosenoT2 * 100) / 100

    // sen
    cosenoT2Sen = Math.sin(coseno * Math.PI / 180)  
    cosenoT2Sen = Math.round(cosenoT2Sen * 100) / 100 
})

botonParteII.addEventListener('click', ()=>{
    let coseno = 0;
    if(cosenoT1 < 0 ){
        cosenoT1 = cosenoT1 * -1
        coseno = cosenoT1 / cosenoT2;


        let resultado = (cosenoT2Sen * coseno) + cosenoT1Sen
        resultado = peso / resultado;
        resultado = Math.round(resultado * 100) / 100 
        let t2 = resultado * coseno
        t2 = Math.round(t2 * 100) / 100 
        resultadoParteIII.innerHTML = `
        <h3>Las Tenciones son:</h3>
        <h4>T1<span> = ${t1}</span></h4> 
        <h4>T2<span> = ${resultado}</span></h4> 
        <h4>T3<span> = ${peso}</span></h4>
        <h3>procedimiento</h3>
        <p>Parte I</p>
        <p>T3y + Py = 0</p>
        <p>T3.sen90° + P.sen270° = 0</p>
        <p>T3.1 + P.-1 = 0</p>
        <p>T3 - P = 0</p>
        <p>T3 = P </p>
        <p>T3 = m.g </p>
        <p>T3 = ${peso}. 9,8| </p>
        <h4>Parte II RX</h4>
        <h5>T1.cos${cosenoT1}° + T2.cos${cosenoT2}° + T3.cos270°</h5> 
        <h1>PROXIMAMENTE</h1>
        <p>para mas info consulte a <a href="https://www.instagram.com/bisiojuan1/?hl=es-la">bisiojuan</a></p>
        
        `;

    }else if(cosenoT2 < 0){
        cosenoT2 = cosenoT2 * -1
        coseno = cosenoT2 / cosenoT1 ;
        

        let resultado = (cosenoT1Sen * coseno) + cosenoT2Sen
        resultado = peso / resultado;
        resultado = Math.round(resultado * 100) / 100 
        let t1 = resultado * coseno
        t1 = Math.round(t1 * 100) / 100 
        resultadoParteIII.innerHTML = `
        <h3>Las Tenciones son:</h3>
        <h4>T1<span> = ${t1}</span></h4> 
        <h4>T2<span> = ${resultado}</span></h4> 
        <h4>T3<span> = ${peso}</span></h4>
        
        <h3>Procedimiento:</h3>
        <h4>Parte I</h4>
        <p>T3y + Py = 0</p>
        <p>T3.sen90° + P.sen270° = 0</p>
        <p>T3.1 + P.-1 = 0</p>
        <p>T3 - P = 0</p>
        <p>T3 = P </p>
        <p>T3 = m.g </p>
        <p>T3 = ${peso}. 9,8</p>
        <h4>Parte II RX</h4>
        <h5>T1.cos${cosenoT1}° + T2.cos${cosenoT2}° + T3.cos270°</h5> 
        <h1>PROXIMAMENTE</h1>
        <p>para mas info consulte a <a href="https://www.instagram.com/bisiojuan1/?hl=es-la">bisiojuan</a></p>
        `;

    }
})   





